"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Loader2 } from "lucide-react";

interface Message {
  role: "user" | "assistant" | "system";
  content: string;
  metadata?: {
    target_domain?: string;
    plan_data?: PentestPlan;
  };
}

interface PentestTool {
  tool: string;
  command: string;
  description: string;
  risk_level: "low" | "medium" | "high";
  category: "network" | "web" | "infrastructure" | "social";
}

interface PentestPlan {
  plan: PentestTool[];
  target: string;
  assessment_type: string;
  estimated_time: string;
  prerequisites: string[];
  warnings: string[];
}

export default function ChatInterface() {
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [currentPlan, setCurrentPlan] = useState<PentestPlan | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim()) return;

    setLoading(true);
    setError("");
    
    try {
      // Add user message
      const userMessage: Message = { role: "user", content: input };
      setMessages((prev) => [...prev, userMessage]);
      setInput(""); // Clear input right away

      // Call the API
      const response = await fetch("/api/chat", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          messages: [...messages, userMessage],
          model_config: {
            model: "ai-pentest-agent",
            temperature: 0.7,
            max_tokens: 2000
          }
        }),
      });

      if (!response.ok) {
        throw new Error(`API request failed: ${response.status}`);
      }

      const data = await response.json();
      
      // Add assistant message with metadata
      const assistantMessage: Message = {
        role: "assistant",
        content: data.message.content,
        metadata: data.message.metadata
      };

      setMessages((prev) => [...prev, assistantMessage]);
      
      // Update current plan if available
      if (data.message.metadata?.plan_data) {
        setCurrentPlan(data.message.metadata.plan_data);
      }

    } catch (err) {
      console.error("Error in handleSubmit:", err);
      setError(err instanceof Error ? err.message : "An unexpected error occurred");
      // Optionally revert the user message if the API call failed
      setMessages((prev) => prev.slice(0, -1));
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card className="bg-white shadow-xl border-0 overflow-hidden">
      <div className="flex flex-col h-[600px]">
        <div className="p-4">
          {loading && (
            <div className="flex items-center gap-2 text-sm text-gray-500 mb-4">
              <Loader2 className="h-4 w-4 animate-spin" />
              Processing...
            </div>
          )}
          
          {error && (
            <Alert variant="destructive" className="mb-4">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
        </div>

        <ScrollArea className="flex-1 px-4 pb-4">
          <div className="space-y-4">
            {messages.map((message, index) => (
              <div
                key={index}
                className={`flex ${
                  message.role === "user" ? "justify-end" : "justify-start"
                }`}
              >
                <div
                  className={`max-w-[80%] p-3 rounded-lg ${
                    message.role === "user"
                      ? "bg-blue-600 text-white"
                      : "bg-white text-gray-900 shadow-sm"
                  }`}
                >
                  {message.content}
                  {message.role === "assistant" && message.metadata?.plan_data && index === messages.length - 1 && (
                    <div className="mt-4 space-y-4">
                      <div className="bg-gray-50 p-4 rounded-lg border">
                        <div className="flex justify-between items-center mb-2">
                          <div className="font-semibold text-blue-600">Target: {message.metadata.plan_data.target}</div>
                          <div className="text-sm text-gray-500">{message.metadata.plan_data.estimated_time}</div>
                        </div>
                        
                        {message.metadata.plan_data.warnings.length > 0 && (
                          <div className="mb-3">
                            <div className="text-red-600 font-semibold mb-1">⚠️ Warnings:</div>
                            <ul className="list-disc list-inside text-sm text-red-500">
                              {message.metadata.plan_data.warnings.map((warning, idx) => (
                                <li key={idx}>{warning}</li>
                              ))}
                            </ul>
                          </div>
                        )}

                        <div className="mb-3">
                          <div className="text-gray-700 font-semibold mb-1">Prerequisites:</div>
                          <ul className="list-disc list-inside text-sm text-gray-600">
                            {message.metadata.plan_data.prerequisites.map((prereq, idx) => (
                              <li key={idx}>{prereq}</li>
                            ))}
                          </ul>
                        </div>

                        <div className="space-y-3">
                          {message.metadata.plan_data.plan.map((step, idx) => {
                            const riskColor = {
                              low: "bg-green-100 text-green-800",
                              medium: "bg-yellow-100 text-yellow-800",
                              high: "bg-red-100 text-red-800"
                            }[step.risk_level];

                            return (
                              <div key={idx} className="bg-white p-3 rounded shadow-sm border">
                                <div className="flex justify-between items-center mb-2">
                                  <div className="font-semibold text-blue-600">{step.tool.toUpperCase()}</div>
                                  <div className={`text-xs px-2 py-1 rounded-full ${riskColor}`}>
                                    {step.risk_level.toUpperCase()}
                                  </div>
                                </div>
                                <div className="font-mono text-sm bg-gray-50 p-2 rounded mb-2">{step.command}</div>
                                <div className="text-gray-600 text-sm">{step.description}</div>
                              </div>
                            );
                          })}
                        </div>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        </ScrollArea>

        <div className="sticky bottom-0 bg-white p-4 border-t">
          <form onSubmit={handleSubmit} className="flex gap-2">
            <Input
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="Enter your pentest request..."
              className="flex-1 h-12 text-base"
              disabled={loading}
              autoComplete="off"
            />
            <Button 
              type="submit" 
              disabled={loading}
              className="bg-blue-600 hover:bg-blue-700 text-white px-8 h-12 min-w-[100px]"
            >
              {loading ? (
                <div className="flex items-center gap-2">
                  <Loader2 className="h-4 w-4 animate-spin" />
                  <span>Sending...</span>
                </div>
              ) : (
                "Send"
              )}
            </Button>
          </form>
        </div>
      </div>
    </Card>
  );
}
